import { install } from "@/plugins/axios";
//
export function loginPhone(phone, password) {
  let timestamp = new Date().getTime();
  return install({
    url: "/login/cellphone",
    method: "post",
    params: {
      phone,
      password,
      timestamp,
    },
  });
}
//
export function loginOut() {
  return install({
    url: "/logout",
  });
}
//
export function loginRefresh() {
  return install({
    url: "/login/refresh",
  });
}

//
export function loginStatus() {
  return install({
    url: "/login/status",
  });
}
