import { install } from "@/plugins/axios";

//
export function detail(uid) {
  return install({
    url: "/user/detail",
    method: "post",
    params: {
      uid,
    },
  });
}
//
export function account() {
  return install({
    url: "/user/account",
    method: "post",
  });
}
// , mv, dj 
export function subcount() {
  return install({
    url: "/user/subcount",
    method: "post",
  });
}
//level
export function level() {
  return install({
    url: "/user/level",
    method: "post",
  });
}
//
export function update() {
  return install({
    url: "/user/update",
    method: "post",
    params: {
      nickname: arguments[0],
      signature: arguments[1],
      gender: arguments[2],
      birthday: arguments[3],
      province: 440000,
      city: 440300,
    },
  });
}
//
export function upload(file) {
  return install({
    url: "/avatar/upload",
    method: "post",
    timeout: 60 * 1000,
    headers: {
      "Content-Type": "multipart/form-data",
    },
    data: file,
  });
}
//
export function playlist(uid, limit, offset) {
  return install({
    url: "/user/playlist",
    method: "post",
    params: {
      uid,
      limit,
      offset,
    },
  });
}
//
export function likelist(uid) {
  return install({
    url: "/likelist",
    method: "post",
    params: {
      uid,
    },
  });
}
//
export function like(id, like) {
  return install({
    url: "/like",
    method: "get",
    params: {
      id,
      like,
    },
  });
}
///
export function follow(id, t) {
  return install({
    url: "/follow",
    method: "post",
    params: {
      id,
      t,
    },
  });
}
//
export function playlistUpdate(id, name, desc, tags) {
  return install({
    url: "/playlist/update",
    method: "post",
    params: {
      id,
      name,
      desc,
      tags,
    },
  });
}
//
export function playlistUpdateImg(file, id) {
  return install({
    url: "/avatar/upload",
    method: "post",
    timeout: 60 * 1000,
    headers: {
      "Content-Type": "multipart/form-data",
    },
    data: file,
    params: { id, param: "168y168" },
  });
}

