import { install } from "@/plugins/axios";
//
export function playDetail(id) {
  return install({
    url: "/playlist/detail",
    method: "post",
    params: {
      id,
    },
  });
}
//
export function playCreate(name, privacy, type) {
  return install({
    url: "/playlist/create",
    method: "post",
    params: {
      name,
      privacy,
      type,
    },
  });
}
//
export function play(name) {
  return install({
    url: "/playlist/detail/dynamic",
    method: "post",
    params: {
      name,
    },
  });
}
///
